import './App.css';
import {
  BrowserRouter, 
  Switch, 
  Route, 
} from "react-router-dom";
import Blogpage from './blog';
import BlogPost from './blog/components/blogpost';

function App() {
  return (
    <div className="App">
        <BrowserRouter>
          <Switch>

            <Route path="/blogpost">
              <BlogPost />
            </Route>
            
            <Route path="/">
              <Blogpage />
            </Route>
            
          </Switch>
        </BrowserRouter>
    </div>
  );
}

export default App;
