import "../static/assets/css/blog.css";
import Navbar from "./components/navbar";
import BlogPostLongFeatured from "./components/blogpost-feature-long";
import SectionFeaturedPosts from "./sections/featured-posts";
import BlogPost from "./components/blogpost";

export default function Blogpage() {
  return (
    <div>
      <div className="container">
        <header className="blog-header py-3">
          <div className="row flex-nowrap justify-content-between align-items-center">
            <div className="col-4 pt-1">
              <a className="link-secondary" href="#">
                Subscribe
              </a>
            </div>
            <div className="col-4 text-center">
              <a className="blog-header-logo text-dark" href="#">
                Large
              </a>
            </div>
            <div className="col-4 d-flex justify-content-end align-items-center">
              <a className="link-secondary" href="#" aria-label="Search">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  fill="none"
                  stroke="currentColor"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  className="mx-3"
                  role="img"
                  viewBox="0 0 24 24"
                >
                  <title>Search</title>
                  <circle cx="10.5" cy="10.5" r="7.5" />
                  <path d="M21 21l-5.2-5.2" />
                </svg>
              </a>
              <a className="btn btn-sm btn-outline-secondary" href="#">
                Sign up
              </a>
            </div>
          </div>
        </header>

        <Navbar />
      </div>

      <main className="container">
        <BlogPostLongFeatured 
          title='Title of a longer featured blog post'
          description = 'Multiple lines of text that form the lede, informing new readers quickly and efficiently about whatâ€™s most interesting in this postâ€™s contents.' 
        />

        <SectionFeaturedPosts></SectionFeaturedPosts>

        <div className="row g-5">
          <div className="col-md-8">
            <h3 className="pb-4 mb-4 fst-italic border-bottom">
              From the Firehose
            </h3>

            <BlogPost></BlogPost>

            <article className="blog-post">
              <h2 className="blog-post-title">New feature</h2>
              <p className="blog-post-meta">
                December 14, 2020 by <a href="#">Chris</a>
              </p>

              <p>
                This is some additional paragraph placeholder content. It has
                been written to fill the available space and show how a longer
                snippet of text affects the surrounding content. We'll repeat it
                often to keep the demonstration flowing, so be on the lookout
                for this exact same string of text.
              </p>
              <ul>
                <li>First list item</li>
                <li>Second list item with a longer description</li>
                <li>Third list item to close it out</li>
              </ul>
              <p>
                This is some additional paragraph placeholder content. It's a
                slightly shorter version of the other highly repetitive body
                text used throughout.
              </p>
            </article>

            <nav className="blog-pagination" aria-label="Pagination">
              <a className="btn btn-outline-primary" href="#">
                Older
              </a>
              <a
                className="btn btn-outline-secondary disabled"
                href="#"
                tabindex="-1"
                aria-disabled="true"
              >
                Newer
              </a>
            </nav>
          </div>

          <div className="col-md-4">
            <div className="position-sticky" style={{top: 2 + 'rem'}}>
              <div className="p-4 mb-3 bg-dark rounded">
                <h4 className="fst-italic">About</h4>
                <p className="mb-0">
                  Customize this section to tell your visitors a little bit
                  about your publication, writers, content, or something else
                  entirely. Totally up to you.
                </p>
              </div>

              <div className="p-4">
                <h4 className="fst-italic">Archives</h4>
                <ol className="list-unstyled mb-0">
                  <li>
                    <a className="link-info" href="#">March 2021</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">February 2021</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">January 2021</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">December 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">November 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">October 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">September 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">August 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">July 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">June 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">May 2020</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">April 2020</a>
                  </li>
                </ol>
              </div>

              <div className="p-4">
                <h4 className="fst-italic">Elsewhere</h4>
                <ol className="list-unstyled">
                  <li>
                    <a className="link-info" href="#">GitHub</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">Twitter</a>
                  </li>
                  <li>
                    <a className="link-info" href="#">Facebook</a>
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </div>
      </main>

      <footer className="blog-footer">
        <p>
          Blog template built for{" "}
          <a href="https://getbootstrap.com/">Bootstrap</a> by{" "}
          <a href="https://twitter.com/mdo">@mdo</a>.
        </p>
        <p>
          <a href="#">Back to top</a>
        </p>
      </footer>
    </div>
  );
}
